.rc-dialog-close {
  display: none;
}

.rc-dialog-body {
  padding: 0;
}

.modal-center {
  top: 50%;
  transform: translateY(-50%);
  height: fit-content;
}

.modal .close-button {
  background: none;
  border: none;
  cursor: pointer;
  line-height: 0;
  padding: 0;
}

.modal .modal-title {
  font-family: "Red Rose";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  color: #333333;
}

.modal .header {
  padding: 24px 32px;
  border-bottom: 1px solid #cfd8dc;
}

.modal .body {
  padding: 32px;
}

.connect-wallet-modal .title {
  font-family: "Red Rose";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #333333;
}

.connect-wallet-modal button {
  padding: 12px 20px;
  background: #f8f9fa;
  border: 1px solid #cfd8dc;
  border-radius: 12px;
  cursor: pointer;
}

.connect-wallet-modal button p {
  font-family: "Sora";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  color: #000000;
}
